.App {
  text-align: center;
}

.App .nav-link{
  text-align: left;

}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 10vmin;
}

.App-header {
  background-color: #282c34;
  /* min-height: 100vh; */
  min-height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

white-text{
  color: #ffffff;
}
.nav-link {
  color: #ffffff;
}

li {
  text-align: left;
  color: #a71b11;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
